import React from 'react';
import PropTypes from "prop-types";
import _ from 'lodash';
import MapResources, {
	climRegionColumnName,
	cropColumnName,
	fidColumnName,
	mapPeriodOptionsHistorie,
	nameColumnName
} from "../../constants/MapResources";

import {outlinesStyle, hoveredStyleDefinition, selectedStyleDefinition} from "../../constants/MapStyles";
import {MapLegend} from "../MapLegend";
import {HoverHandler} from "@gisatcz/ptr-core";
import {MapPopup} from "../MapPopup";
import {WorldWindMap, ReactLeafletMap, MapControls as MapControlsPresentation, MapSetPresentationMap as PresentationMap, MapSet as MapSetPresentation} from "@gisatcz/ptr-maps";
import {MapInfo} from "../MapInfo";
import {Select} from "@gisatcz/ptr-atoms";
import utils from "../../utils";
import moment from "moment";
import {LineChartPopup} from "../LineChartPopup";
import {LineChart} from '@gisatcz/ptr-charts';
import Fade from "react-reveal/Fade";

import image1 from "../../assets/img/dlouhodoby_normal_figure1.png";


const getChoroplethStyle = (attributeKey) => {
	return utils.fillStyleTemplate(
		{
			attributeKey,
			"attributeClasses":[
				{
					"interval": [0,50],
					"intervalBounds": [false, false],
					"fill": "#ff0000"
				},
				{
					"interval": [50,70],
					"intervalBounds": [true, false],
					"fill": "#ff8888"
				},
				{
					"interval": [70,90],
					"intervalBounds": [true, false],
					"fill": "#ffaa00"
				},
				{
					"interval": [90,110],
					"intervalBounds": [true, false],
					"fill": "#ffff00"
				},
				{
					"interval": [110,130],
					"intervalBounds": [true, false],
					"fill": "#b0e000"
				},
				{
					"interval": [130,150],
					"intervalBounds": [true, false],
					"fill": "#6fc400"
				},
				{
					"interval": [150,300],
					"intervalBounds": [true, false],
					"fill": "#38a800"
				}
			]
		}
	)
};

class Historie extends React.PureComponent {
	static propTypes = {
		data: PropTypes.array,
		placeView: PropTypes.object,
		scope: PropTypes.object,
		activePeriodKey: PropTypes.string
	};

	constructor(props) {
		super(props);

		this.state = {
			activeDpbKey: props.data && props.data[0].properties[fidColumnName],
			selectedMapPeriod: mapPeriodOptionsHistorie[3]
		};

		this.onMapClick = this.onMapClick.bind(this);
		this.onMapPeriodChange = this.onMapPeriodChange.bind(this);
	}

	componentDidUpdate(prevProps, prevState, snapshot) {
		const props = this.props;
		if (props.data) {
			let exists = this.getSelectedAreaData();
			if (!exists) {
				this.setState({
					activeDpbKey: props.data && props.data[0] && props.data[0].properties[fidColumnName]
				});
			}
		}
	}

	onMapClick(mapKey, layerKey, fids) {
		this.setState({
			activeDpbKey: fids[0]
		})
	}

	onMapPeriodChange(option) {
		this.setState({
			selectedMapPeriod: option
		});
	}

	render() {
		const props = this.props;

		let dataForCharts = null;

		let attribute = "P" + this.state.selectedMapPeriod.key;
		let mapChoroplethStyle = getChoroplethStyle(attribute);


		let mapLayers = [];

		if (this.state.activeDpbKey) {
			dataForCharts = this.prepareDataForCharts();
		}

		if (this.props.data) {
			mapLayers = this.getMapLayers(mapChoroplethStyle);
		}

		return (
			<>
				<div className="tacrAgritas-section">
					<div>
						<h1>{props.scope && props.scope.data.nameDisplay}</h1>
						<div className="tacrAgritas-section-subtitle">Srovnn aktulnho stavu zemdlsk porost s dlouhodobm normlem (1985 - 2015)</div>
						<p>Srovnn aktulnho stavu s dlouhodobm normlem je zaloeno na zpracovn dlouhodob asov ady druicovch dat Landsat, obsahujc piblin 8500 scn pozench v prbhu obdob 1985  2015. Analzou tchto druicovch snmk byly stanoveny tzv. normlov kivky popisujc obvykl asov vvoj hodnot vegetanho indexu NDVI pro rzn zemdlsk plodiny za danch klimatickch podmnek. Aktuln hodnoty vegetanho indexu NDVI (zjitn pomoc druicovch dat pozench v prbhu aktuln vegetan sezny) jsou potom srovnvny s hodnotou dlouhodobho normlu (pro danou plodinu, termn a klimatick region). Vsledek je vyjden jako procentuln podl aktuln hodnoty ku hodnot dlouhodobho normlu (tj. 100 % = aktuln hodnota je prv rovna hodnot dlouhodobho normlu pro danou plodinu, termn a klimatick region). Srovnn aktulnho stavu s dlouhodobm normlem je pak realizovno prbn vdy 2x do msce (1.  15. a 16.  30./31. dne v danm msci). Vsledky jsou v tomto ppad agregovny na rove jednotlivch zemdlskch pozemk.</p>

						<div className="tacrAgritas-map-component-wrapper">
							{this.renderMapSet('map-set-historie', mapLayers, attribute, "%")}
							<MapLegend
								style={mapChoroplethStyle}
								name={"Aktuln hodnota NDVI vs. dlouhodob norml"}
								unit={"%"}
								noData
							/>
						</div>
						{dataForCharts ? this.renderChart(dataForCharts) : null}
						<p>Uren referennch normlovch kivek vegetanho indexu NDVI pro jednotliv zemdlsk plodiny a klimatick regiony bylo zaloeno na zpracovn kompletnho archivu druicovch dat Landsat pro eskou republiku. Toto zpracovn zahrnovalo celkem piblin 8500 scn pozench v obdob mezi roky 1985  2015. V prvn fzi zpracovn byly na podklad tchto scn vytvoeny plodinov mapy pro vechny vegetan sezny uvedenho obdob 1985  2015. Provdn analzy asovho prbhu hodnot vegetanho indexu specificky pro jednotliv plodiny je zcela klov, vzhledem k odlinostem v asovm vvoji jednotlivch plodin (nap. ozim obiloviny ve srovnn s cukrovkou apod.). Ze sri druicovch dat byly nsledn pro jednotliv kombinace plodina  klimatick region extrahovny temporln profily indexu NDVI (popisujc vvoj hodnot tohoto indexu v ase). Tyto kivky pak byly pevedeny do jednotnho asovho mtka (uvaovna byla agregace dat do 15-dennch interval). Z dlch kivek potom byla vygenerovna tzv. normlov kivka popisujc z dlouhodobho hlediska obvykl prbh hodnot indexu NDVI pro danou plodinu a klimatick region. Klimatick regiony, uvaovan v rmci tto analzy, vychzej z regionalizace pouvan v systmu bonitovanch pdn-ekologickch jednotek (BPEJ).</p>
						<p>V dal fzi jsou hodnoty NDVI prbn extrahovny z druicovch dat pozench v prbhu aktuln vegetan sezny (opt vdy v 15-dennch monitorovacch oknech). Aby byla maximalizovna pravdpodobnost, e analyzovan pozemek bude v rmci danho monitorovacch obdob zachycen bez oblanosti, jsou pro sledovn aktulnch hodnot indexu NDVI vyuvna nejen druicov data Landsat, ale souasn i Sentinel-2. S ohledem na ponkud odlin radiometrick charakteristiky senzor OLI (Landsat-8) a MSI (Sentinel-2) je v tomto ppad ovem nutn na data Sentinel-2 nejprve aplikovat proces radiometrick harmonizace, dky nmu jsou hodnoty indexu NDVI odvozen z dat Sentinel-2 a Landsat-8 navzjem srovnateln. Aktuln hodnoty indexu NDVI jsou v nsledujcm kroku srovnvny s hodnotou odvozenou z normlov kivky pro danou plodinu a dan klimatick region. Vsledn mapov produkt tak vyjaduje procentuln rove aktuln hodnoty indexu NDVI vi normlov hodnot (100 %). Srovnn aktulnho vvoje s dlouhodobm normlem je mon pro jednotliv zemdlsk parcely vyjdit rovn v podob grafu zobrazujcm prbh aktuln temporln kivky indexu NDVI a jeho srovnn s prbhem kivky normlov.</p>
						<p>V dal fzi jsou hodnoty NDVI prbn extrahovny z druicovch dat pozench v prbhu aktuln vegetan sezny (opt vdy v 15-dennch monitorovacch oknech). Aby byla maximalizovna pravdpodobnost, e analyzovan pozemek bude v rmci danho monitorovacch obdob zachycen bez oblanosti, jsou pro sledovn aktulnch hodnot indexu NDVI vyuvna nejen druicov data Landsat, ale souasn i Sentinel-2. S ohledem na ponkud odlin radiometrick charakteristiky senzor OLI (Landsat-8) a MSI (Sentinel-2) je v tomto ppad ovem nutn na data Sentinel-2 nejprve aplikovat proces radiometrick harmonizace, dky nmu jsou hodnoty indexu NDVI odvozen z dat Sentinel-2 a Landsat-8 navzjem srovnateln. Aktuln hodnoty indexu NDVI jsou v nsledujcm kroku srovnvny s hodnotou odvozenou z normlov kivky pro danou plodinu a dan klimatick region. Vsledn mapov produkt tak vyjaduje procentuln rove aktuln hodnoty indexu NDVI vi normlov hodnot (100 %). Srovnn aktulnho vvoje s dlouhodobm normlem je mon pro jednotliv zemdlsk parcely vyjdit rovn v podob grafu zobrazujcm prbh aktuln temporln kivky indexu NDVI a jeho srovnn s prbhem kivky normlov.</p>
					</div>
					<Fade left distance="50px">
						<div className="tacrAgritas-image">
							<img src={image1}/>
							<p className="tacrAgritas-component-description">
								Ukzka srovnn aktulnho stavu porostu ozim penice (A) a jarnho jemene (B) vzhledem k dlouhodobmu normlu (1985 - 2015). Po rychlm nrstu na potku vegetan sezny, kter byl dn zejmna neobvykle teplm poasm v dubnu (vvoj nkterch plodin byl urychlen o 30  40 dn), zaal vlivem nastupujcho sucha stav porost v polovin kvtna stagnovat a v prbhu ervna klesl a k podprmrnm hodnotm.
							</p>
						</div>
					</Fade>
				</div>
			</>
		);
	}

	renderMapSet(key, layers, valueColumnName, unit) {
		const selectedArea = this.getSelectedAreaData();

		return (
			<Fade left distance="50px">
				<div className="tacrAgritas-map-set-container single-map">
					<HoverHandler
						popupContentComponent={
							<MapPopup
								valueColumnName={valueColumnName}
								unit={unit}
							/>
						}>
						<MapSetPresentation
							activeMapKey={key}
							mapComponent={ReactLeafletMap}
							view={this.props.placeView}
							sync={{
								boxRange: true,
								center: true,
								tilt: true,
								heading: true,
								roll: true
							}}
							backgroundLayer={MapResources.cartoDbLight}
						>
							<PresentationMap
								mapKey={key+'-map-1'}
								layers={layers}
								onLayerClick={this.onMapClick}
							/>
							<MapControlsPresentation zoomOnly levelsBased/>
							<MapInfo
								cropName={selectedArea && selectedArea.properties[cropColumnName]}
								selectedAreaName={selectedArea && selectedArea.properties[nameColumnName]}
								selectedAreaClimRegion={selectedArea && selectedArea.properties[climRegionColumnName]}
							/>
							<Select
								className="tacrAgritas-map-label"
								value={this.state.selectedMapPeriod}
								optionLabel="label"
								optionValue="key"
								options={mapPeriodOptionsHistorie}
								onChange={this.onMapPeriodChange}
							/>
						</MapSetPresentation>
					</HoverHandler>
				</div>
			</Fade>
		);
	}

	renderChart(data) {
		const selectedArea = this.getSelectedAreaData();

		return (
			<div className="tacrAgritas-chart-wrapper">
				<HoverHandler
					popupContentComponent={LineChartPopup}
				>
					<div className="tacrAgritas-chart-title">Pdn blok<em> {selectedArea && selectedArea.properties[nameColumnName]}</em></div>
					<LineChart
						key="history"

						data={data}
						keySourcePath="key"
						nameSourcePath="name"
						serieDataSourcePath="data"
						xSourcePath="date"
						ySourcePath="value"
						colorSourcePath="color"

						isSerie
						pointRadius={3}

						xScaleType="time"
						xValuesSize={4}
						xOptions={{
							name: "Time",
							axisValueFormat: "MMMM",
							popupValueFormat: "D. MMMM YYYY",
							min: `${this.props.activePeriodKey}-02-28T00:00:00.000Z`,
							max: `${this.props.activePeriodKey}-11-01T00:00:00.000Z`,
						}}

						yLabel
						yValuesSize={3}
						yOptions={{
							name: "NDVI"
						}}
						withoutYbaseline={false}
						legend
					/>
				</HoverHandler>
			</div>
		);
	}

	getSelectedAreaData() {
		return _.find(this.props.data, (feature) => feature.properties[fidColumnName] === this.state.activeDpbKey);
	}

	getMapLayers(style) {
		return [
			{
				key: "test",
				layerKey: "test",
				type: "vector",
				opacity: 0.7,

				options: {
					features: this.props.data,
					style: style.data.definition,
					hovered: {
						style: hoveredStyleDefinition
					},
					selected: {
						'test': {
							style: selectedStyleDefinition,
							keys: [this.state.activeDpbKey],
						}
					},
					fidColumnName
				}
			}
		]
	}

	prepareDataForCharts() {
		let history = [];
		let current = [];

		let feature = this.getSelectedAreaData();

		if (feature) {
			_.forIn(feature.properties, (value, key) => {
				let attributeParts = key.split("_");
				let attribute = attributeParts[0];

				if (attribute.length === 5) {
					const attributeCode = attribute.substring(0,1);
					const dateCode = attribute.substring(1,5);
					const dateCodeNumber = Number(attribute.substring(1,5));

					if (!_.isNaN(dateCodeNumber)) {
						const day = dateCode.substring(2,4);
						const month = dateCode.substring(0,2);
						const date = moment(`${this.props.activePeriodKey}-${month}-${day}`).toISOString();

						const dateEndCode = attributeParts[1];
						const dayEnd = dateEndCode.substring(2,4);
						const monthEnd = dateEndCode.substring(0,2);
						const dateEnd = moment(`${this.props.activePeriodKey}-${monthEnd}-${dayEnd}`).toISOString();

						const record = {date, dateEnd, value};

						if (value) {
							if (attributeCode === "R") {
								history.push(record);
							} else if (attributeCode === "C") {
								current.push(record);
							}
						}
					}
				}
			});

			return [
				{
					...feature.properties,
					key: "history",
					name: "Dlouhodob norml",
					data: history,
					color: "#aaaaaa"

				},{
					...feature.properties,
					key: "current",
					name: "Aktuln hodnota",
					data: current,
					color: "#ff00ff"

				}
			];
		} else {
			return null;
		}
	}
}

export default Historie;