import React from 'react';
import PropTypes from "prop-types";
import _ from 'lodash';
import MapResources, {
	climRegionColumnName,
	cropColumnName,
	fidColumnName, fenologyOptions,
	nameColumnName
} from "../../constants/MapResources";
import {outlinesStyle, hoveredStyleDefinition, selectedStyleDefinition} from "../../constants/MapStyles";
import phenology from "../../assets/img/phenology.png";
import productivity from "../../assets/img/productivity.png";
import Fade from "react-reveal/Fade";
import {HoverHandler} from "@gisatcz/ptr-core";
import {MapPopup} from "../MapPopup";
import {WorldWindMap, ReactLeafletMap, MapControls as MapControlsPresentation, MapSetPresentationMap as PresentationMap, MapSet as MapSetPresentation} from "@gisatcz/ptr-maps";
import {MapInfo} from "../MapInfo";
import {Select} from "@gisatcz/ptr-atoms";
import {MapLegend} from "../MapLegend";
import HorizontalBarInfoGraphic from "../HorizontalBarInfoGraphic";
import config from "../../config";

class Produktivita extends React.PureComponent {
	static propTypes = {

	};

	constructor(props) {
		super(props);

		this.state = {
			rasters: PropTypes.object,
			activeDpbKey: props.data && props.data[0].properties[fidColumnName],
			selectedAttribute: fenologyOptions[0],
			showChoropleth: true
		};

		this.onMapClick = this.onMapClick.bind(this);
		this.onAttributeChange = this.onAttributeChange.bind(this);
		this.onShowChoropleth = this.onShowChoropleth.bind(this);
	}

	componentDidUpdate(prevProps, prevState, snapshot) {
		const props = this.props;
		if (props.data) {
			let exists = this.getSelectedAreaData();
			if (!exists) {
				this.setState({
					activeDpbKey: props.data && props.data[0] && props.data[0].properties[fidColumnName]
				});
			}
		}
	}

	onMapClick(mapKey, layerKey, fids) {
		this.setState({
			activeDpbKey: fids[0]
		})
	}

	onAttributeChange(option) {
		this.setState({
			selectedAttribute: option
		});
	}

	onShowChoropleth() {
		this.setState({
			showChoropleth: !this.state.showChoropleth
		});
	}

	render() {
		const props = this.props;
		const selectedArea = this.getSelectedAreaData();
		let selectedAttribute = this.state.selectedAttribute;
		let layers = [];
		let dataForChart = null;

		if (this.props.data && this.state.selectedAttribute) {
			layers = this.getMapLayers(this.state.showChoropleth ? selectedAttribute.style : outlinesStyle);
		}

		if (this.props.data && this.state.activeDpbKey) {
			dataForChart = this.getDataForChart();
		}

		return (
			<>
				<div className="tacrAgritas-section">
					<div>
						<h1>Fenologick a produkn charakteristiky porost zemdlsk plodin</h1>
						<p>
							Fenologie se zabv popisem asovho prbhu ivotnch projev rostlin (a ivoich). Jejm clem je pedevm uren termn periodicky se opakujcch stav (tzv. fenofz), jakmi je v ppad rostlin napklad kveten i senescence. I kdy vtinu projev monitorovanch v rmci pozemnch fenologickch pozorovn nelze pmo zachytit pomoc dat dlkovho przkumu, pesto lze na zklad zpracovn asovch ad druicovch snmk zskat mnoho dleitch informac popisujcch asov vvoj rostlinnho porostu (nap. termny zatku, konce a vrcholu vegetan sezny, dlku vegetan sezny apod.). Vvoj vegetace v ase pak souasn souvis i s celkovou produktivitou porostu.
						</p>
						<p>
							Mapov vrstvy pedstavuj hodnoty celkem 14 rznch fenologickch charakteristik, odvozench pomoc matematick analzy asovho vvoje hodnot vegetanch index NDVI a NRERI zskanch z druicovch dat Sentinel-2. Tyto hodnoty jsou prezentovny jak v rastrov podob (tj. na rovni pixelu), tak jako hodnoty agregovan na rove jednotlivch zemdlskch pozemk. Obdobn je pak ureno celkem 6 indiktor produktivity porost.
						</p>

						<div className="tacrAgritas-map-component-wrapper">
							<div className="tacrAgritas-map-layers">
								<label>
									<input type="checkbox" checked={this.state.showChoropleth} onChange={this.onShowChoropleth}/>
									<div>Zobraz hodnoty agregovan na pdn blok</div>
								</label>
							</div>
							<Fade left distance="50px">
								<div className="tacrAgritas-map-set-container single-map">
									<HoverHandler
										popupContentComponent={
											<MapPopup
												valueColumnName={selectedAttribute && selectedAttribute.key}
												unit={selectedAttribute && selectedAttribute.unit}
											/>
										}
									>
										<MapSetPresentation
											activeMapKey={"productivity-map-set"}
											mapComponent={ReactLeafletMap}
											view={this.props.placeView}
											backgroundLayer={MapResources.cartoDbLight}
										>
											<PresentationMap
												mapKey={'map-1'}
												layers={layers}
												onLayerClick={this.onMapClick}
											/>
											<MapControlsPresentation zoomOnly levelsBased/>
											<MapInfo
												cropName={selectedArea && selectedArea.properties[cropColumnName]}
												selectedAreaName={selectedArea && selectedArea.properties[nameColumnName]}
												selectedAreaClimRegion={selectedArea && selectedArea.properties[climRegionColumnName]}
											/>
											<Select
												className="tacrAgritas-map-label wide"
												value={this.state.selectedAttribute}
												optionLabel="longName"
												optionValue="key"
												options={fenologyOptions}
												onChange={this.onAttributeChange}
											/>
										</MapSetPresentation>
									</HoverHandler>
								</div>
							</Fade>
							<MapLegend
								style={selectedAttribute && selectedAttribute.style}
								name={selectedAttribute && selectedAttribute.name}
								unit={selectedAttribute && selectedAttribute.unit}
								noData
							/>
						</div>
						{dataForChart ? this.renderChart(dataForChart) : null}
					</div>
				</div>

				<div className="tacrAgritas-section">
					<div>
						<h2>Popis fenologickch charakteristik</h2>
						<div className="tacrAgritas-fenology-list">
							<div className="tacrAgritas-fenology-list-item">
								<div>SOS (A)</div>
								<div>Datum zatku vegetan sezny vyjden v DOY</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>EOS (B)</div>
								<div>Datum konce vegetan sezny vyjden v DOY</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>LOS_GRW (C)</div>
								<div>Dlka rstov fze vegetan sezny vyjden ve dnech (doba od zatku do vrcholu vegetan sezny)</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>LOS_SEN (D)</div>
								<div>Dlka senescenn fze vegetan sezny vyjden ve dnech (doba od vrcholu do konce vegetan sezny) </div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>LOS_TOT (E)</div>
								<div>Celkov dlka vegetan sezny vyjden ve dnech (doba od zatku do konce vegetan sezny)</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>BASE_GRW (F)</div>
								<div>Bze zatku vegetan sezny  poten stav (hodnota minima ped zatkem vegetan sezny)</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>BASE_SEN (G)</div>
								<div>Bze konce vegetan sezny  koncov stav (hodnota minima po konci vegetan sezny) </div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>CLIMAX_DOY (H)</div>
								<div>Datum dosaen seznnho maxima vyjden v DOY</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>CLIMAX_VI (I)</div>
								<div>Hodnota vegetanho indexu v termnu maxima</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>AMPLITUDE (J)</div>
								<div>Seznn amplituda hodnot vegetanho indexu</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>GRW_DOY (K)</div>
								<div>Termn dosaen maximln rychlosti nrstu hodnoty vegetanho indexu vyjden v DOY</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>GRW_RATE (L)</div>
								<div>Maximln rychlost nrstu hodnoty vegetanho indexu</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>SEN_DOY (M)</div>
								<div>Termn dosaen maximln rychlosti poklesu hodnoty vegetanho indexu vyjden v DOY</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>SEN_RATE (N)</div>
								<div>Maximln rychlost poklesu hodnoty vegetanho indexu</div>
							</div>

							<Fade left distance="50px">
								<div className="tacrAgritas-image">
									<img style={{padding: '1rem'}} src={phenology}/>
									<p className="tacrAgritas-component-description">Pozn.: DOY = Day Of Year, tj. poadov slo dne v danm roce. 1.1. = DOY 1, 2.1. = DOY 231.12. = DOY 365 (v nepestupnm roce)</p>
								</div>
							</Fade>
						</div>

						<h2>Popis produknch charakteristik</h2>
						<div className="tacrAgritas-fenology-list">

							<div className="tacrAgritas-fenology-list-item">
								<div>LINT_GRW (O)</div>
								<div>Indiktor hrub produkce v rstov fzi vegetan sezny</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>LINT_SEN (P)</div>
								<div>Indiktor hrub produkce v senescenn fzi vegetan sezny</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>LINT_TOT (Q)</div>
								<div>Indiktor hrub produkce za vegetan seznu</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>SINT_GRW (R)</div>
								<div>Indiktor ist produkce v rstov fzi vegetan sezny</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>SINT_SEN (S)</div>
								<div>Indiktor ist produkce v senescenn fzi vegetan sezny</div>
							</div>

							<div className="tacrAgritas-fenology-list-item">
								<div>SINT_TOT (T)</div>
								<div>Indiktor ist produkce za vegetan seznu</div>
							</div>
						</div>

						<Fade left distance="50px">
							<div className="tacrAgritas-image">
								<img style={{padding: '1rem'}} src={productivity}/>
								<p className="tacrAgritas-component-description">Pozn.: indiktor hrub produkce koreluje s celkovm mnostvm biomasy porostu, zatmco indiktor ist produkce bere v potaz pouze nrst biomasy nad rove bze na zatku vegetan sezny. Ani v jednom ppad se vak nejedn o kvantitativn odhad tzv. hrub primrn produkce (gross primary production - GPP) i ist primrn produkce (net primary production - NPP). </p>
							</div>
						</Fade>
					</div>
				</div>
			</>
		);
	}

	renderChart(data) {
		return (
			<div className="tacrAgritas-fenology-chart">
				{data.map((item, index) => {
					return (
						<HorizontalBarInfoGraphic
							key={index}
							name={item.longName}
							unit={item.unit}
							min={item.min}
							max={item.max}
							mean={item.mean}
							value={item.currentValue}
						/>
					);
				})}
			</div>
		);
	}


	getSelectedAreaData() {
		return _.find(this.props.data, (feature) => feature.properties[fidColumnName] === this.state.activeDpbKey);
	}

	getMapLayers(style) {
		let attributeKey = this.state.selectedAttribute.key;
		let wmsLayerName = this.props.rasters[attributeKey];

		let layers =  [
			{
				key: "test",
				layerKey: "test",
				type: "vector",
				opacity: 1,

				options: {
					features: this.props.data,
					style: style && style.data.definition,
					hovered: {
						style: hoveredStyleDefinition
					},
					selected: {
						'test': {
							style: selectedStyleDefinition,
							keys: [this.state.activeDpbKey],
						}
					},
					fidColumnName
				}
			}
		];

		if (wmsLayerName) {
			layers.unshift(
				{
					key: "wms",
					type: "wms",
					opacity: 1,

					options: {
						url: config.tacrAgritasGeoserverWmsUrl,
						params: {
							layers: wmsLayerName
						}
					}
				}
			);
		}

		return layers;
	}

	getDataForChart() {
		const selectedAreaData = this.getSelectedAreaData();

		if (selectedAreaData) {
			return fenologyOptions.map(option => {
				let attributeValues = this.props.data.map(item => {
					return item.properties[option.key];
				});

				let sortedValues = _.sortBy(attributeValues);

				return {
					...option,
					min: sortedValues[0],
					max: sortedValues[sortedValues.length -1],
					mean: sortedValues[Math.floor(sortedValues.length/2)],
					currentValue: selectedAreaData.properties[option.key]
				}
			});
		} else {
			return null;
		}
	}
}

export default Produktivita;