import React from "react";
import _ from "lodash";
import {NavLink} from "@gisatcz/ptr-state";
import Helmet from "react-helmet";
import Fade from "react-reveal/Fade";
import Jump from "react-reveal/Jump";
import {Icon} from '@gisatcz/ptr-atoms';

export const PageIndex = props => {
		const places = props.places;

	return (
		<div className="tacrAgritas-index">
			<Helmet
				defaultTitle="AGRITAS portl"
			/>
			<div className="tacrAgritas-index-background">
			</div>
			<div className="tacrAgritas-index-content">
				<Fade duration={1500}>
					<h1><Icon icon="crop"/>AGRITAS portl</h1>
					<p>Biofyzikln monitoring, fenologick a produkn charakteristiky porost zemdlsk plodin, Srovnn aktulnho stavu zemdlsk porost s dlouhodobm normlem (1985 - 2015).</p>
					<div className="tacrAgritas-index-cta">
						<Jump delay={1500}>
							<Icon icon="arrow-left"/>
						</Jump>
						<span>Zante vbrem farmy</span>
					</div>
				</Fade>

				<Fade left cascade distance="50px">
					<div className="tacrAgritas-index-cards">
						{places.map(place => {
							const tagKeys = place.data.tagKeys;
							if (tagKeys && _.includes(tagKeys, "propole")) {
								return null;
							} else {
								return (
									<NavLink
										key={place.key}
										to={"/" + place.key}
										className={`tacrAgritas-index-card ${place.key} ${place.data.bbox ? "" : "disabled"}`}
									>
										<div className="tacrAgritas-index-card-content">
											<div className="tacrAgritas-index-card-title">{place.data.nameDisplay}</div>
											<div className="tacrAgritas-index-card-subtitle">{place.data.description}</div>
										</div>
									</NavLink>
								);
							}
						})}
					</div>
				</Fade>
			</div>
			<div className="tacrAgritas-index-footer">
				<Fade duration={1500}>
					<p>Vvoj tohoto mapovho portlu a metodiky pro kvantitativn odhad hodnot biofyziklnch charakteristik porost zemdlskch plodin byl podpoen Technologickou Agenturou esk Republiky (TA R) v rmci projektu TH02030248 "Vyuit druicovch dat Copernicus pro efektivn monitoring stavu a managementu vybranch rostlinnch agrosystm".</p>
					<p> <a href="http://gisat.cz" target="_blank">Gisat s.r.o.</a> a <a href="http://www.czechglobe.cz/cs/" target="_blank">stav vzkumu globln zmny AV R v.v.i.</a>, {`${new Date().getFullYear()}`}</p>
				</Fade>
			</div>
		</div>
	);
};