import React from 'react';
import config from "../../config";
import { ReactBnbGallery } from 'react-bnb-gallery';

const imagesRepo = config.tacrAgritasImagesRepositoryUrl;

class Potencial extends React.PureComponent {
	static propTypes = {

	};

	constructor(props) {
		super(props);
		this.state = {
			galleryOpen: false
		};
		this.toggleGallery = this.toggleGallery.bind(this);

	}

	toggleGallery(index) {
		this.setState(prevState => ({
			galleryOpen: !prevState.galleryOpen,
			activePhotoIndex: index
		}));
	}

	render() {
		let images = null;
		if (this.props.images) {
			images = this.props.images.map(image => {
				return {
					photo: imagesRepo + image.source,
					thumbnail: imagesRepo + image.thumbnailSource,
					caption: image.title,
					subcaption: image.description
				}
			});
		}

		return (
			<div className="tacrAgritas-section">
				<div>
					<h1>Mapa rstovho potencilu zemdlskch plodin</h1>

					<div className="tacrAgritas-photo-gallery">
						{images.map((image, index) =>
							<div className="tacrAgritas-photo-gallery-item">
								<div>{image.caption}</div>
								<img onClick={this.toggleGallery.bind(this, index)} src={image.thumbnail}/>
							</div>
						)}
					</div>
					<ReactBnbGallery
						show={this.state.galleryOpen}
						photos={images}
						onClose={this.toggleGallery}
						activePhotoIndex={this.state.activePhotoIndex}
					/>

					<p>Mapa vychz z prostorov a asov analzy zapojenosti zemdlskch porost pomoc druicovch dat.
						Pedmtem zpracovn je minimln desetilet asov ada druicovch snmk vybranch tak, aby co nejlpe
						postihly hlavn fze rstu zemdlskch plodin v kadm roce. Mapa je zpracovna samostatn pro vybran
						plodiny, kter se na sledovanm pozemku pstuj opakovan (minimln ve 3 vegetanch obdobch). V rmci
						vyhodnocen prostorov variability zapojenosti porost je provedena zonace s clem identifikovat dlouhodob
						problematick plochy danho pozemku pro kadou sledovanou plodinu. Roky, ve kterch jsou zjitny ppadn
						nahodil i vjimen stavy vegetace, jsou z vyhodnocen vyazeny.</p>
					<p>Na zklad multitemporln analzy asov ady harmonizovanch map zapojenosti porost z celho
						sledovanho obdob je nejprve vyhodnocena variabilita jednotlivch plodin na danm pozemku. Clem tohoto
						kroku je vybrat pozemky dlouhodob vykazujc vznamnou variabilitu. Pro tyto pozemky je nsledn provedena
						asoprostorov segmentace cel asov ady, na jejm zklad je provedena kategorizace zapojenosti a
						delineace zn. Na zvr je urena referenn zna s dlouhodob nejvy prmrnou zapojenost a pro kadou
						dal znu je vypoten jej nevyuit prmrn potencil vzhledem k zn referenn. Vslednm produktem
						celho zpracovn je vektorov vrstva management zn s piazenm relativnm potencilem kad zny.</p>
				</div>
			</div>
		);
	}
}

export default Potencial;