import React from 'react';
import PropTypes from "prop-types";
import _ from 'lodash';
import moment from "moment";
import {LineChart} from '@gisatcz/ptr-charts';
import {HoverHandler} from "@gisatcz/ptr-core";
import {Select} from "@gisatcz/ptr-atoms";
import {WorldWindMap, ReactLeafletMap, MapControls as MapControlsPresentation, MapSetPresentationMap as PresentationMap, MapSet as MapSetPresentation} from "@gisatcz/ptr-maps";
import MapResources, {cropColumnName, fidColumnName, nameColumnName, climRegionColumnName, mapPeriodOptions} from "../../constants/MapResources";
import {outlinesStyle, hoveredStyleDefinition, selectedStyleDefinition} from "../../constants/MapStyles";

import utils from "../../utils";
import {LineChartPopup} from "../LineChartPopup";
import {MapInfo} from "../MapInfo";
import {MapLegend} from "../MapLegend";
import {MapPopup} from "../MapPopup";
import Fade from "react-reveal/Fade";

import image1 from "../../assets/img/biofyzika_figure1.png";
import image2 from "../../assets/img/biofyzika_figure2.png";
import image3 from "../../assets/img/biofyzika_figure3.png";
import image4 from "../../assets/img/biofyzika_figure4.png";
import image5 from "../../assets/img/biofyzika_figure5.png";
import config from "../../config";

const getZonesStyle = () => {
	return utils.fillStyleTemplate(
		{
			"attributeValues":[
				{
					"value": 0,
					"fill": "#000000"
				},{
					"value": 1,
					"fill": "#d7191c"
				},{
					"value": 2,
					"fill": "#fdae61"
				},{
					"value": 3,
					"fill": "#ffffbf"
				}, {
					"value": 4,
					"fill": "#a6d96a"
				}
			]
		}
	)
};


const getChlorophyllChoroplethStyle = (attributeKey) => {
	return utils.fillStyleTemplate(
	{
			attributeKey,
			"attributeClasses":[
				{
					"interval": [0,15],
					"intervalBounds": [false, false],
					"fill": "#ba1414"
				},
				{
					"interval": [15,20],
					"intervalBounds": [true, false],
					"fill": "#d14e30"
				},
				{
					"interval": [20,25],
					"intervalBounds": [true, false],
					"fill": "#e37d52"
				},
				{
					"interval": [25,30],
					"intervalBounds": [true, false],
					"fill": "#f2ae79"
				},
				{
					"interval": [30,35],
					"intervalBounds": [true, false],
					"fill": "#fce4a7"
				},
				{
					"interval": [35,40],
					"intervalBounds": [true, false],
					"fill": "#e9f5ae"
				},
				{
					"interval": [40,45],
					"intervalBounds": [true, false],
					"fill": "#b9db86"
				},
				{
					"interval": [45,50],
					"intervalBounds": [true, false],
					"fill": "#8cc263"
				},
				{
					"interval": [50,55],
					"intervalBounds": [true, false],
					"fill": "#61ab41"
				},
				{
					"interval": [55,100],
					"intervalBounds": [true, false],
					"fill": "#369121"
				}
			]
		}
	)
};

const getWaterChoroplethStyle = (attributeKey) => {
	return utils.fillStyleTemplate(
		{
			attributeKey,
			"attributeClasses":[
				{
					"interval": [0,0.010],
					"intervalBounds": [false, false],
					"fill": "#d62f27"
				},
				{
					"interval": [0.010,0.015],
					"intervalBounds": [true, false],
					"fill": "#e66043"
				},
				{
					"interval": [0.015,0.020],
					"intervalBounds": [true, false],
					"fill": "#f28d61"
				},
				{
					"interval": [0.020,0.025],
					"intervalBounds": [true, false],
					"fill": "#fab984"
				},
				{
					"interval": [0.025,0.030],
					"intervalBounds": [true, false],
					"fill": "#ffe9ad"
				},
				{
					"interval": [0.030,0.035],
					"intervalBounds": [true, false],
					"fill": "#e9edbe"
				},
				{
					"interval": [0.035,0.040],
					"intervalBounds": [true, false],
					"fill": "#c0ccbe"
				},
				{
					"interval": [0.040,0.045],
					"intervalBounds": [true, false],
					"fill": "#99aebd"
				},
				{
					"interval": [0.045,0.050],
					"intervalBounds": [true, false],
					"fill": "#6e8fb8"
				},
				{
					"interval": [0.050,0.060],
					"intervalBounds": [true, false],
					"fill": "#4575b5"
				}
			]
		}
	)
};

const getLeafsChoroplethStyle = (attributeKey) => {
	return utils.fillStyleTemplate(
		{
			attributeKey,
			"attributeClasses":[
				{
					"interval": [0,1],
					"intervalBounds": [false, false],
					"fill": "#61150d"
				},
				{
					"interval": [1,1.5],
					"intervalBounds": [true, false],
					"fill": "#7a391b"
				},
				{
					"interval": [1.5,2],
					"intervalBounds": [true, false],
					"fill": "#915d2c"
				},
				{
					"interval": [2,2.5],
					"intervalBounds": [true, false],
					"fill": "#a88640"
				},
				{
					"interval": [2.5,3],
					"intervalBounds": [true, false],
					"fill": "#c2b659"
				},
				{
					"interval": [3,3.5],
					"intervalBounds": [true, false],
					"fill": "#b5bd5b"
				},
				{
					"interval": [3.5,4],
					"intervalBounds": [true, false],
					"fill": "#849c44"
				},
				{
					"interval": [4,4.5],
					"intervalBounds": [true, false],
					"fill": "#5b7d31"
				},
				{
					"interval": [4.5,5],
					"intervalBounds": [true, false],
					"fill": "#356120"
				},
				{
					"interval": [5,15],
					"intervalBounds": [true, false],
					"fill": "#104510"
				}
			]
		}
	)
};

class Biofyzika extends React.PureComponent {
	static propTypes = {
		data: PropTypes.array,
		rasters: PropTypes.object,
		placeView: PropTypes.object,
		scope: PropTypes.object,
		activePeriodKey: PropTypes.string
	};

	constructor(props) {
		super(props);

		this.state = {
			activeDpbKey: props.data && props.data[0].properties[fidColumnName],
			mapView: props.activePlaceView,
			selectedMapPeriod: mapPeriodOptions[3],
			showChoropleth: true
		};

		this.onMapViewChange = this.onMapViewChange.bind(this);
		this.onMapClick = this.onMapClick.bind(this);
		this.onMapPeriodChange = this.onMapPeriodChange.bind(this);
		this.onShowChoropleth = this.onShowChoropleth.bind(this);
	}

	componentDidMount() {
		if (this.props.placeView) {
			this.setState({mapView: this.props.placeView});
		}
	}

	componentDidUpdate(prevProps, prevState, snapshot) {
		const props = this.props;
		if (props.data) {
			let exists = this.getSelectedAreaData();
			if (!exists) {
				this.setState({
					activeDpbKey: props.data && props.data[0] && props.data[0].properties[fidColumnName]
				});
			}
		}

		if (prevProps.placeView !== this.props.placeView) {
			this.setState({mapView: this.props.placeView});
		}

		if ((prevProps.activePlace !== this.props.activePlace) && this.props.activePlace.key === "AdwAgro") {
			this.setState({
				selectedMapPeriod: mapPeriodOptions[6]
			});
		}
	}

	onMapViewChange(view) {
		const self = this;

		if (this.mapViewTimeout) {
			clearTimeout(this.mapViewTimeout);
		}

		this.mapViewTimeout = setTimeout(() => {
			self.setState({
				mapView: view
			})
		}, 1000);
	}

	onMapClick(mapKey, layerKey, fids) {
		this.setState({
			activeDpbKey: fids[0]
		})
	}

	onMapPeriodChange(option) {
		this.setState({
			selectedMapPeriod: option
		});
	}

	onShowChoropleth() {
		this.setState({
			showChoropleth: !this.state.showChoropleth
		});
	}

	render() {
		const props = this.props;

		let dataForCharts = null;

		let zonesStyle = getZonesStyle();

		let chlorophyllAttribute = "C" + this.state.selectedMapPeriod.key;
		let chlorophyllStyle = getChlorophyllChoroplethStyle(chlorophyllAttribute);

		let waterAttribute = "W" + this.state.selectedMapPeriod.key;
		let waterStyle = getWaterChoroplethStyle(waterAttribute);

		let leafsAttribute = "L" + this.state.selectedMapPeriod.key;
		let leafsStyle = getLeafsChoroplethStyle(leafsAttribute);

		let chlorophyllFirstMapLayers, chlorophyllSecondMapLayers = [];
		let waterFirstMapLayers, waterSecondMapLayers = [];
		let leafsFirstMapLayers, leafsSecondMapLayers = [];

		if (this.state.activeDpbKey) {
			dataForCharts = this.prepareDataForCharts();
		}

		if (this.props.data) {
			chlorophyllFirstMapLayers = this.getMapLayers(this.state.showChoropleth ? chlorophyllStyle : outlinesStyle, "cab");
			chlorophyllSecondMapLayers = this.getMapLayers(outlinesStyle, "cab_zonace");

			waterFirstMapLayers = this.getMapLayers(this.state.showChoropleth ? waterStyle : outlinesStyle, "cw");
			waterSecondMapLayers = this.getMapLayers(outlinesStyle, "cw_zonace");

			leafsFirstMapLayers = this.getMapLayers(this.state.showChoropleth ? leafsStyle : outlinesStyle, "lai");
			leafsSecondMapLayers = this.getMapLayers(outlinesStyle, "lai_zonace");
		}

		return (
			<>
				<div className="tacrAgritas-section">
					<div>
						<h1>{props.scope && props.scope.data.nameDisplay}</h1>
						<p>Biofyzikln monitoring pedstavuje vizualizaci vsledk kvantitativnho odhadu hodnot vybranch biofyziklnch charakteristik porost zemdlskch plodin: <b>obsahu chlorofylu (C<sub>ab</sub>), obsahu vody (C<sub>w</sub>) a indexu listov plochy (LAI)</b>. Vlastn vpoet tchto parametr je zaloeno na zpracovn druicovch dat Sentinel-2. Vsledn hodnoty jednotlivch parametr jsou potom agregovny vdy po 10-dennch asovch obdobch (vdy 1.  10., 11. -  20. a 21.  30./31. dne v danm msci). Z rovn druicovch snmk jsou pak hodnoty dlch biofyziklnch charakteristik dle prostorov agregovny na rove jednotlivch zemdlskch pozemk. Zskan odhady biofyziklnch charakteristik jsou nsledn pouity pro vymezen tzv. management zn v rmci monitorovanch zemdlskch pozemk. Tato zonace je pak zaloena na srovnn aktuln zjitnch hodnot danho parametru s hodnotami obvyklmi pro danou plodinu, termn a klimatick podmnky. </p>
					</div>
				</div>
				<div className="tacrAgritas-section">
					<div>
						<h2>Obsah chlorofylu</h2>
						<p>Vrstva obsah chlorofylu vyjaduje obsah fotosynteticky aktivnch pigment v listov vegetace pepoten na listovou plochu (g/cm<sup>2</sup>). Mnostv listovch pigment velmi tsn souvis s celkovou fotosyntetickou aktivitou rostlin, kter je pak dle navzna na vivu porostu (napklad duskem).</p>
						<p>Obsah chlorofylu je tedy jednou ze zkladnch vstup pro tvorbu map pro variabiln aplikace (nap. aplikace hnojiv bhem produknho a kvalitativnho hnojen). Pi sledovn asov ady obsahu chlorofylu je pak mon i zhodnotit dopady provedench agrotechnickch opaten (opt zejmna hnojen, ppadn aplikace POR). V prbhu dozrvn (obilniny, epka apod.) dochz v dsledku celkovho proschn porost k pirozenmu bytku obsahu chlorofylu. Dky tomu je mon sledovat nejen aktuln stav zralosti porost, ale tak ppadn selektivn dozrvn porostu v rmci jednoho pozemku. Hodnoty obsahu chlorofylu se u zdravch porost pohybuj nejastji v rozmez mezi 20  60 g/cm<sup>2</sup>. U zralch/suchch porost pak obsah chlorofylu kles zpravidla pod 10 g/cm<sup>2</sup>.</p>
						<p className="tacrAgritas-component-description">
							Mapov okno vlevo znzoruje vsledky kvantitativnho odhadu obsahu chlorofylu v absolutn hodnot (g/cm<sup>2</sup>), a to jak na rovni druicovch dat Sentinel-2, tak jako hodnoty agregovan za jednotliv zemdlsk pozemky. Mapov okno vpravo znzoruje vymezen tzv. management zn na zklad prostorov variability hodnot obsahu chlorofylu a jejich srovnn s hodnotami obvyklmi pro danou plodinu, termn a klimatick podmnky.
						</p>
						<div className="tacrAgritas-map-component-wrapper">
							<div className="tacrAgritas-map-layers">
								<label>
									<input type="checkbox" checked={this.state.showChoropleth} onChange={this.onShowChoropleth}/>
									<div>Zobraz hodnoty agregovan na pdn blok</div>
								</label>
							</div>
							{this.renderMapSet('map-set-1', chlorophyllFirstMapLayers, chlorophyllSecondMapLayers, chlorophyllAttribute, "g/cm2")}
							<div className="tacrAgritas-legend-set">
								<MapLegend
									style={chlorophyllStyle}
									name={"Obsah chlorofylu"}
									unit={"g/cm2"}
									noData
								/>
								<MapLegend
									style={zonesStyle}
									name={"Management zny"}
								/>
							</div>
						</div>
						{dataForCharts && dataForCharts.chlorophyll ? this.renderChlorophyllChart(dataForCharts) : null}
					</div>
				</div>
				<div className="tacrAgritas-section">
					<div>
						<h2>Obsah vody</h2>
						<p>
							Obsah vody je v tomto ppad vyjden v podob tzv. Equivalent Water Thickness (EWT) popisujc tlouku vrstviky vody, kter by se vytvoila, pokud bychom vekerou vodu obsaenou v listov vyseparovali a rovnomrn rozprosteli po ploe listu. Mnostv vody je tedy vyjdeno v centimetrech (cm). Obsah vody je obecn velmi dynamick parametr popisujc aktuln zsobenost rostlin vodou. Dky tomu je mon pomoc tto datov vrstvy sledovat ppadn ohroen rostlin nedostatkem vody (nap. vlivem sucha). Podobn jako v ppad obsahu chlorofylu kles pirozen obsah vody v prbhu dozrvn (obilniny, epka apod.). Proto je mon datovou vrstvu vyut i pro sledovn selektivnho dozrvn plodin. Obdobnm zpsobem je pak mon sledovat napklad ptomnost rznch anomli (nap. podmench ploch apod.). Hodnoty obsahu vody se u erstv zelen vegetace pohybuj nejastji v rozmez 0.0100  0.0550 cm, u suchch, zrajcch porost se pak zpravidla obsah vody pohybuje v rozmez 0.0005  0.0100 cm.
						</p>
						<p className="tacrAgritas-component-description">
							Mapov okno vlevo znzoruje vsledky kvantitativnho odhadu obsahu vody v absolutn hodnot (cm), a to jak na rovni druicovch dat Sentinel-2, tak jako hodnoty agregovan za jednotliv zemdlsk pozemky. Mapov okno vpravo znzoruje vymezen tzv. management zn na zklad prostorov variability hodnot obsahu vody a jejich srovnn s hodnotami obvyklmi pro danou plodinu, termn a klimatick podmnky.
						</p>
						<div className="tacrAgritas-map-component-wrapper">
							<div className="tacrAgritas-map-layers">
								<label>
									<input type="checkbox" checked={this.state.showChoropleth} onChange={this.onShowChoropleth}/>
									<div>Zobraz hodnoty agregovan na pdn blok</div>
								</label>
							</div>
							{this.renderMapSet('map-set-2', waterFirstMapLayers, waterSecondMapLayers, waterAttribute, "cm")}
							<div className="tacrAgritas-legend-set">
								<MapLegend
									style={waterStyle}
									name={"Obsah vody"}
									unit={"cm"}
									noData
								/>
								<MapLegend
									style={zonesStyle}
									name={"Management zny"}
								/>
							</div>
						</div>
						{dataForCharts && dataForCharts.water ? this.renderWaterChart(dataForCharts) : null}
					</div>
				</div>
				<div className="tacrAgritas-section">
					<div>
						<h2>Index listov plochy</h2>
						<p>Index listov plochy (LAI) je definovn jako jednostrann plocha listov vegetace vztaen na jednotkovou plochou povrchu. Jedn se tedy o bezrozmrnou veliinu (resp. jej jednotkou by byly m<sup>2</sup>/m<sup>2</sup>). LAI je dleitm parametrem k urovn mnoha dalch biologickch a fyziklnch proces souvisejcch s vegetac nebo de facto popisuje plochu, na n dochz k pjmu slunenho zen a dalch ltek ze zemsk atmosfry. Pomoc hodnot indexu listov plochy lze sledovat pedevm zapojenost porostu a nrst mnostv biomasy v ase, eho lze vyut pi pprav podklad pro variabiln aplikace hnojiv pi produknm a kvalitativnm hnojen, a nsledn t pro sledovn jejich dopad. Hodnoty indexu listov plochy se mohou pro rzn plodiny a rzn stdia vvoje tchto plodin pomrn vrazn liit.</p>
						<p className="tacrAgritas-component-description">
							Mapov okno vlevo znzoruje vsledky kvantitativnho odhadu indexu listov plochy v absolutn hodnot (m<sup>2</sup>/m<sup>2</sup>), a to jak na rovni druicovch dat Sentinel-2, tak jako hodnoty agregovan za jednotliv zemdlsk pozemky. Mapov okno vpravo znzoruje vymezen tzv. management zn na zklad prostorov variability hodnot indexu listov plochy a jejich srovnn s hodnotami obvyklmi pro danou plodinu, termn a klimatick podmnky.
						</p>
						<div className="tacrAgritas-map-component-wrapper">
							<div className="tacrAgritas-map-layers">
								<label>
									<input type="checkbox" checked={this.state.showChoropleth} onChange={this.onShowChoropleth}/>
									<div>Zobraz hodnoty agregovan na pdn blok</div>
								</label>
							</div>
							{this.renderMapSet('map-set-3', leafsFirstMapLayers, leafsSecondMapLayers, leafsAttribute, "m2/m2")}
							<div className="tacrAgritas-legend-set">
								<MapLegend
									style={leafsStyle}
									name={"Index listov plochy"}
									unit={<>m<sup>2</sup>/m<sup>2</sup></>}
									noData
								/>
								<MapLegend
									style={zonesStyle}
									name={"Management zny"}
								/>
							</div>
						</div>
						{dataForCharts && dataForCharts.leafs ? this.renderLeafsChart(dataForCharts) : null}
					</div>
				</div>
				{this.renderMethodology()}
			</>
		);
	}

	renderMapSet(key, firstMapLayers, secondMapLayers, valueColumnName, unit) {
		const selectedArea = this.getSelectedAreaData();

		return (
			<Fade left distance="50px">
				<div className="tacrAgritas-map-set-container">
					<HoverHandler
						popupContentComponent={
							<MapPopup
								valueColumnName={valueColumnName}
								unit={unit}
							/>
						}
					>
						<MapSetPresentation
							activeMapKey={key}
							mapComponent={ReactLeafletMap}
							view={this.state.mapView}
							onViewChange={this.onMapViewChange}
							sync={{
								boxRange: true,
								center: true,
								tilt: true,
								heading: true,
								roll: true
							}}
							backgroundLayer={MapResources.cartoDbLight}
						>
							<PresentationMap
								mapKey={key+'-map-1'}
								layers={firstMapLayers}
								onLayerClick={this.onMapClick}
							/>
							<PresentationMap
								mapKey={key+'-map-2'}
								layers={secondMapLayers}
								onLayerClick={this.onMapClick}
							/>
							<MapControlsPresentation zoomOnly levelsBased/>
							<MapInfo
								cropName={selectedArea && selectedArea.properties[cropColumnName]}
								selectedAreaName={selectedArea && selectedArea.properties[nameColumnName]}
								selectedAreaClimRegion={selectedArea && selectedArea.properties[climRegionColumnName]}
							/>
							<Select
								className="tacrAgritas-map-label"
								value={this.state.selectedMapPeriod}
								optionLabel="label"
								optionValue="key"
								options={mapPeriodOptions}
								onChange={this.onMapPeriodChange}
							/>
						</MapSetPresentation>
					</HoverHandler>
				</div>
			</Fade>
		);
	}

	renderChlorophyllChart(data) {
		const selectedArea = this.getSelectedAreaData();

		return (
			<div className="tacrAgritas-chart-wrapper">
				<HoverHandler
					popupContentComponent={LineChartPopup}
				>
					<div className="tacrAgritas-chart-title">Pdn blok<em> {selectedArea && selectedArea.properties[nameColumnName]}</em></div>
					<LineChart
						key="chlorophyll"

						data={[data]}
						keySourcePath="ID_DPB"
						nameSourcePath="NKOD_DPB"
						serieDataSourcePath="chlorophyll"
						xSourcePath="date"
						ySourcePath="value"
						colorSourcePath="color"

						isSerie
						pointRadius={3}

						xScaleType="time"
						xValuesSize={4}
						xOptions={{
							name: "Time",
							axisValueFormat: "MMMM",
							popupValueFormat: "D. MMMM YYYY",
							min: `${this.props.activePeriodKey}-02-28T00:00:00.000Z`,
							max: `${this.props.activePeriodKey}-11-01T00:00:00.000Z`,
						}}

						yLabel
						yValuesSize={3}
						yOptions={{
							unit: "g/cm2",
							name: "Obsah chlorofylu"
						}}
						withoutYbaseline={false}

					/>
					<p className="tacrAgritas-component-description">
						asov vvoj obsahu chlorofylu (g/cm<sup>2</sup>) pro vybran zemdlsk pozemek.
					</p>
				</HoverHandler>
			</div>
		);
	}

	renderWaterChart(data) {
		const selectedArea = this.getSelectedAreaData();

		return (
			<div className="tacrAgritas-chart-wrapper">
				<HoverHandler
					popupContentComponent={LineChartPopup}
				>
					<div className="tacrAgritas-chart-title">Pdn blok<em> {selectedArea && selectedArea.properties[nameColumnName]}</em></div>
					<LineChart
						key="water"

						data={[data]}
						keySourcePath="ID_DPB"
						nameSourcePath="NKOD_DPB"
						serieDataSourcePath="water"
						xSourcePath="date"
						ySourcePath="value"
						colorSourcePath="color"

						isSerie
						pointRadius={3}

						xScaleType="time"
						xValuesSize={4}
						xOptions={{
							name: "Time",
							axisValueFormat: "MMMM",
							popupValueFormat: "D. MMMM YYYY",
							min: `${this.props.activePeriodKey}-02-28T00:00:00.000Z`,
							max: `${this.props.activePeriodKey}-11-01T00:00:00.000Z`,
						}}

						yLabel
						yValuesSize={3}
						yOptions={{
							unit: "cm",
							name: "Obsah vody"
						}}
						withoutYbaseline={false}

					/>
					<p className="tacrAgritas-component-description">
						asov vvoj obsahu vody (cm) pro vybran zemdlsk pozemek.
					</p>
				</HoverHandler>
			</div>
		);
	}

	renderLeafsChart(data) {
		const selectedArea = this.getSelectedAreaData();

		return (
			<div className="tacrAgritas-chart-wrapper">
				<HoverHandler
				popupContentComponent={LineChartPopup}
				>
				<div className="tacrAgritas-chart-title">Pdn blok<em> {selectedArea && selectedArea.properties[nameColumnName]}</em></div>
					<LineChart
						key="leafs"

						data={[data]}
						keySourcePath={fidColumnName}
						nameSourcePath={nameColumnName}
						serieDataSourcePath="leafs"
						xSourcePath="date"
						ySourcePath="value"
						colorSourcePath="color"

						isSerie
						pointRadius={3}

						xScaleType="time"
						xValuesSize={4}
						xOptions={{
							name: "Time",
							axisValueFormat: "MMMM",
							popupValueFormat: "D. MMMM YYYY",
							min: `${this.props.activePeriodKey}-02-28T00:00:00.000Z`,
							max: `${this.props.activePeriodKey}-11-01T00:00:00.000Z`,
						}}

						yLabel
						yValuesSize={3}
						yOptions={{
							unit: "m2/m2",
							name: "Index listov plochy"
						}}
						withoutYbaseline={false}

					/>
					<p className="tacrAgritas-component-description">
						asov vvoj indexu listov plochy (m<sup>2</sup>/m<sup>2</sup>) pro vybran zemdlsk pozemek.
					</p>
				</HoverHandler>
			</div>
		);
	}

	renderMethodology() {
		return (
			<div className="tacrAgritas-section methodology">
				<div>
					<h3>Kvantitativn odhad biofyziklnch charakteristik porost zemdlskch plodin</h3>
					<p>Kvantitativn odhad hlavnch biofyziklnch parametr je zaloen na obecn skutenosti, e biofyzikln parametry ovlivuj spektrln charakteristiky porost sledovan pomoc druicovch dat. Vzjemn funkn vztah mezi spektrln signaturou a biofyziklnmi parametry porost je v ppad naich produkt een metodou aplikace modelu penosu zen PROSAIL (radiative transfer model). Tento koncept je zaloen na vyuit matematickho modelu popisujcho penos zen pi jeho interakci s povrchem (vegetac). Vstupem modelu je simulace spektrln signatury porostu pro danou kombinaci biofyziklnch (a jinch) parametr. Krom vlastnch biofyziklnch charakteristik porost (jako jsou napklad obsahy listovch pigment, vody a suiny, index listov plochy aj.) bere model v potaz parametry popisujc strukturu porostu (nap. orientaci listov v prostoru), vliv geometrie pozen obrazovch dat (nap. poloha Slunce, smr pohledu senzoru apod.) a vliv spektrlnch charakteristik pdy. Hlavn vhodou vyuit konceptu modelu penosu zen je skutenost, e pomoc nj meme nasimulovat spektrln signatury porost pro velk rozsahy hodnot jednotlivch vstupnch parametr, jejich pokryt relnm pozemnm vzorkovnm by bylo prakticky nerealizovateln. Akoliv je model penosu zen zaloen na obecnch fyziklnch zkonech, je nejprve nutn jej parametrizovat (zkalibrovat) tak, aby vsledn simulace byly realistick. V ppad naich produkt je optimalizace pouitho modelu provedena specificky pro kadou zjmovou plodinu. Tm je zajitn nejlep mon soulad modelovanch hodnot s realitou.</p>
					<p>
						Vlastn odhad jednotlivch biofyziklnch parametr je nsledn realizovn s vyuitm nstroj tzv. strojovho uen (machine learning) jejich uen probh pomoc databze nasimulovanch spektrlnch signatur jednotlivch plodin, po nm je algoritmus aplikovn na reln data Sentinel-2. Finlnm vstupem ve popsanho procesu je kvantitativn odhad t hlavn biofyziklnch parametr porost: <b>obsah chlorofylu (C<sub>ab</sub>)</b> vyjden v g/cm<sup>2</sup>, <b>obsah vody (C<sub>w</sub>)</b> vyjden jako tzv. Equivalent Water Thickness (tlouka vrstvy, kter by vznikla rozprostenm veker obsaen vody po ploe listu) v cm a <b>index listov plochy (LAI)</b>, kter je bezrozmrnou jednotkou a vyjaduje podl plochy listov porostu vi jednotkov ploe povrchu.
					</p>

					{this.renderImageContainer(image1, "Ukzka odhadu obsahu chlorofylu pro porosty ozim penice v rznm stupni zralosti (20.6. 2017)")}
					{this.renderImageContainer(image2, "Ukzka odhadu obsahu vody pro porost jarnho jemene. Nap zemdlskm pozemkem vede star zazemnn n rameno Labe. Pda je v tomto mst vlh, co se vrazn projev jak na rychlost dozrvn porostu, tak na jeho obsahu vody.")}
					{this.renderImageContainer(image3, "Ukzka odhadu indexu listov plochy pro porosty ozim penice. Srovnn hustho (zapojenho) a dkho (nezapojenho) porostu (1.4. 2017).")}

					<p>
						Krom zkladnho vstupu zobrazujcho odhadovan hodnoty jednotlivch parametr v pvodnch fyziklnch jednotkch je souasn vytvoena i mapa tzv. management zn, sdruujcch vdy sti porostu s podobnmi vlastnostmi. Oproti obdobnm eenm zaloenm na analze jednoduchch vegetanch index (nap. NDVI) je zde vhoda pedevm v tom, e ppadn rozdly v charakteru porostu v dlch stech pozemku je mon pesnji specifikovat. Zaazen do urit management zny zohleduje aktuln pstovanou plodinu, klimatick region, do kterho dan pozemek spad, a datum, ke nm je vsledek analzy vztaen. Rozsahy hodnot jednotlivch parametr jsou pro dl management zny odvozeny ze statistick analzy 4333 zemdlskch pozemk z let 2016  2019 a pedstavuj kvantily (Q<sub>0.25</sub>, Q<sub>0.50</sub> a Q<sub>0.75</sub>) hodnot danho biofyziklnho parametru pro danou plodinu, klimatick region a datum. Zna 1 tak pedstavuje hodnoty spadajc mezi 25 % nejnich hodnot pro danou plodinu, klimatick region a as, zatmco zna 4 analogicky zahrnuje hodnoty spadajc mezi 25 % nejvych hodnot pro danou plodinu, klimatick region a as.
					</p>

					{this.renderImageContainer(image4, "Pehled referennch zemdlskch pozemk pro uren normlovch hodnot jednotlivch biofyziklnch parametr a jejich vvoje v ase.")}
					{this.renderImageContainer(image5, <>Ukzka seznnho vvoje indexu listov plochy (LAI) zjmovch zemdlskch plodin (skupin plodin) v klimatickm regionu T2 a principu vymezen jednotlivch management zn pomoc kvantil Q<sub>0.25</sub>, Q<sub>0.50</sub> a Q<sub>0.75</sub></>)}
				</div>
			</div>
		);
	}

	renderImageContainer(imgSrc, description) {
		return (
			<Fade left distance="50px">
				<div className="tacrAgritas-image">
					<img src={imgSrc}/>
					<p className="tacrAgritas-component-description">
						{description}
					</p>
				</div>
			</Fade>
		);
	}

	getMapLayers(style, themeKey) {
		const rastersForTheme = this.props.rasters && this.props.rasters[themeKey];
		const periodStartString = this.state.selectedMapPeriod.key.split("_")[0];
		const wmsLayerName = rastersForTheme && rastersForTheme[`${this.props.activePeriodKey}${periodStartString}`];

		let layers = [
			{
				key: "test",
				layerKey: "test",
				type: "vector",
				opacity: 1,

				options: {
					features: this.props.data,
					style: style.data.definition,
					hovered: {
						style: hoveredStyleDefinition
					},
					selected: {
						'test': {
							style: selectedStyleDefinition,
							keys: [this.state.activeDpbKey],
						}
					},
					fidColumnName
				}
			}
		];

		if (wmsLayerName) {
			layers.unshift({
				key: "wms",
				type: "wms",
				opacity: 1,

				options: {
					url: config.tacrAgritasGeoserverWmsUrl,
					params: {
						layers: wmsLayerName
					}
				}
			});
		}

		return layers;
	}

	prepareDataForCharts() {
		let chlorophyll = [];
		let water = [];
		let leafs = [];

		let feature = this.getSelectedAreaData();

		if (feature) {
			_.forIn(feature.properties, (value, key) => {
				let attributeParts = key.split("_");
				let attribute = attributeParts[0];

				if (attribute.length === 5) {
					const attributeCode = attribute.substring(0,1);
					const dateCode = attribute.substring(1,5);
					const dateCodeNumber = Number(attribute.substring(1,5));

					if (!_.isNaN(dateCodeNumber)) {
						const day = dateCode.substring(2,4);
						const month = dateCode.substring(0,2);
						const date = moment(`${this.props.activePeriodKey}-${month}-${day}`).toISOString();

						const dateEndCode = attributeParts[1];
						const dayEnd = dateEndCode.substring(2,4);
						const monthEnd = dateEndCode.substring(0,2);
						const dateEnd = moment(`${this.props.activePeriodKey}-${monthEnd}-${dayEnd}`).toISOString();

						const record = {date, dateEnd, value};

						if (value) {
							if (attributeCode === "C") {
								chlorophyll.push(record);
							} else if (attributeCode === "W") {
								water.push(record);
							} else if (attributeCode === "L") {
								leafs.push(record);
							}
						}
					}
				}
			});

			return {...feature.properties, chlorophyll, water, leafs, color: "#ff00ff"};
		} else {
			return null;
		}
	}

	getSelectedAreaData() {
		return _.find(this.props.data, (feature) => feature.properties[fidColumnName] === this.state.activeDpbKey);
	}
}

export default Biofyzika;